import { useState } from "react";
import { BrowserRouter, Route, Routes, Link } from "react-router-dom";

import Vintage from "./pages/Vintage";
import Basic from "./pages/Basic";
import ModalPopup from "./components/ModalPopup";

function App() {
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [modalContentSel, setModalContentSel] = useState("");
  const [modalStyle, setModalStyle] = useState("");
  const handleOpenKModal = (style) => {
    setModalStyle(style);
    setModalContentSel("K");
    setModalIsOpen(true);
  };
  const handleOpenJModal = (style) => {
    setModalStyle(style);
    setModalContentSel("J");
    setModalIsOpen(true);
  };
  const handleCloseModal = () => {
    setModalIsOpen(false);
  };

  return (
    <BrowserRouter basename="/">
      <div className="App">
        <div style={{ color: "white", fontSize: "14px" }}>
          <Link
            to="/basic"
            style={{ display: "inline-block", padding: "10px 20px" }}
          >
            BASIC
          </Link>
          <Link
            to="/vintage"
            style={{ display: "inline-block", padding: "10px 20px" }}
          >
            VINTAGE
          </Link>
        </div>
        <Routes>
          <Route
            path="/basic"
            element={
              <Basic
                openKModal={handleOpenKModal}
                openJModal={handleOpenJModal}
              />
            }
          />
          <Route
            path="/vintage"
            element={
              <Vintage
                openKModal={handleOpenKModal}
                openJModal={handleOpenJModal}
              />
            }
          />
        </Routes>
        <ModalPopup
          isOpen={modalIsOpen}
          closeModal={handleCloseModal}
          contentSel={modalContentSel}
          modalStyle={modalStyle}
        />
      </div>
    </BrowserRouter>
  );
}

export default App;
